export const filterTime =  (val) => {
            if (!val) return '';
            // : 1495159106281 => 2017/5/19 9:58
            // : new Date(val).getTime()
            let creaTime = new Date(val).getTime(),
                curTime = new Date().getTime(),
                $Date = new Date(val),
                year = $Date.getFullYear(),    // 
                month = $Date.getMonth() + 1,  // 
                date = $Date.getDate(),        // 
                diffTime = curTime - creaTime,                // 
                diffSecounds = Math.floor(diffTime / 1000),   // 
                diffMinutes = Math.floor(diffSecounds / 60),  // 
                diffHours = Math.floor(diffMinutes / 60),     // 
                diffDays = Math.floor(diffHours / 24);        // 
            
            if (diffMinutes === 0) {
                return diffSecounds + ''
            }
            if (diffHours === 0) {
                return diffMinutes + ''
            }
            if (diffDays === 0) {
                return diffHours + ''
            }
            if (diffDays > 0 && diffDays <= 20) {
                return diffDays + ''
            }
            if (diffDays > 20) {
                return year + '-' + month + '-' + date
            }
        }


