import * as types from '../constants/types'

export const commonActions = {    
    /**
     *  snackbar 
     * @param {msg} [String]      snackbar 
     * @param {isWarn} [Boolean]  
     * @param {position} [String] snackbar 
     */
    showSnackbarAction({ commit, state }, params) {
        let isWarn = params.isWarn || false,
            position = params.position || '56px';

        commit(types.SHOW_SNACK);              //  snack
        commit(types.DEFINE_SNACK_MSG, {       // 
            msg: params.msg
        });

        //  snack 
        commit(types.DEFINE_SNACK_TYPE, {
            isWarn
        });

        // 
        commit(types.DEFINE_SNACK_POSITION, {
            position
        })  

        // 2s  snackbar
        setTimeout(() => {
            commit(types.HIDE_SNACK)
        }, 2000);
    }
}


